<template>
    <div class="herostat">
        <h1 class="divided">英雄等级</h1>
        <go-back />
        <div v-if="mainLoading">
             <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template  v-if="!mainLoading">
        <h3>概要等级属性</h3>
        <table border="3">
            <thead>
                <th>英雄等级</th>
                <th>所需经验</th>
                <th>等级属性</th>
            </thead>
                <tr v-for="(item,index) in result.mark" :key="index">
                    <td>{{item.level}}</td>
                    <td>{{item.exp}}</td>
                    <td>
                    <stat-grid
                        :statSet="statSet(item.state)"
                    />
                    </td>
                </tr>
        </table>
        <h3>浏览每一级属性</h3>
        英雄等级：
        <input type="text" v-model.number="level"/>
        <button class="search-btn" @click="searchLevel(level)">查询</button>
        <button class="search-btn" @click="reset">清空</button>
        <div v-if="loading">
             <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <table border="1" v-if="!loading">
            <thead>
                <th>英雄等级</th>
                <th>所需经验</th>
                <th>等级属性</th>
            </thead>
                <tr v-for="(item,index) in result.all" :key="index">
                    <td>{{item.level}}</td>
                    <td>{{item.exp}}</td>
                    <td>
                    <stat-grid
                        :statSet="statSet(item.state)"
                    />
                    </td>
                </tr>
        </table>
        </template>
    </div>
</template>

<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import StatGrid from "@/old/game/StatGrid";
import ItemStat from "@/old/api/item/itemstat";
import Loader from "@/components/util/Loader";
Vue.component("load-indicator", Loader);
export default {
    components: {
        GoBack,
        StatGrid,
        Loader
    },
    data(){
        return{
            result: null,
            error: null,
            loading: true,
            mainLoading: true,
            level: 0,
        }
    },
    created(){
        this.fetchData();
    },
    watch:{
        level(){
            if(this.level == 0){
                this.result = GameInfo.herostatCache;
            }
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.herostatCache){
                this.result = GameInfo.herostatCache;
                this.loading = false;
                this.mainLoading = false;
            }else{
                GameInfo.herostat(this.$store.state.regionCode,0,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                        this.mainLoading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                        this.mainLoading = false;
                    });
                }
        },
        statSet(state) {
            let ret = ItemStat.joinStatSet(state,"value");
            return ret;
        },
        searchLevel(levelId){
            if(levelId<=0 || levelId>3000){
                return;
            }
            this.loading = true;
            GameInfo.herostat(this.$store.state.regionCode,levelId,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
            });
        },
        reset(){
            this.level = 0;
        }
    }
}
</script>
<style>
.search-btn{
    height:1.8em;
    margin-left:5px;
}
</style>