<template>
    <div>
        <go-back />
        <h1 class="divided">称号大全</h1>
        <div v-if="loading && collectionLoading">
            <load-indicator loadText="正在加载称号"></load-indicator>
        </div>
        
        <template v-if="!collectionLoading">
            <h3 style="text-align:center;">收藏进度</h3>
            <table style="margin:0 auto;">
                <thead>
                    <th>阶段</th>
                    <th>收集数</th>
                    <th>奖励称号</th>
                    <th>称号描述</th>
                </thead>
                <tr v-for="(v,k) in collection" :key="k">
                    <td>{{v.id}}</td>
                    <td>{{v.goal}}</td>
                    <td><span :style="titleStyle(v.title._TextInColor,v.title._TextOutColor)">{{v.title.name}}</span></td>
                    <td>{{v.title.description}}</td>
                </tr>
            </table>
            </template>

            <h3>图鉴</h3>
            搜索
            <input  type="text" placeholder="输入称号名称搜索..." v-model="searchName"/>
            <button @click="searchData">查询</button>
            <button @click="resetfield">重置</button>

            <div v-if="loading && !collectionLoading">
                <load-indicator loadText="正在搜索"></load-indicator>
            </div>

            <template v-if="!loading">
                <table style="margin-top:1em;" border="2">
                    <thead>
                        <th>称号</th>
                        <th>描述</th>
                        <th>属性</th>
                        <th>英雄称号属性</th>
                        <th>关联物品</th>
                        <th>期限天数</th>
                    </thead>
                        <tr v-for="(v,k) in result.titles" :key="k" style="height:75px;">
                            <td><span :style="titleStyle(v._TextInColor,v._TextOutColor)">{{v.name}}</span></td>
                            <td>{{v.description}}</td>
                            <td v-if="v.state.length>0">
                                <stat-grid :statSet="statSet(v.state)"/>
                            </td>
                            <td v-else>无</td>
                            <td v-if="v.fixState.length>0">
                                <stat-grid :statSet="statSet(v.fixState)"/>
                            </td>
                            <td v-else>无</td>
                            <td v-if="v.item">
                                <item-icon-tooltip
                                    :itemId="v.item.id" 
                                    :itemStub="v.item"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                            </td>
                            <td v-else>无</td>
                            <td v-if="v.days!=0">{{v.days}}天</td>
                            <td v-else>无期限</td>
                        </tr>
                </table>
                <pager :pageSize="pageSize" v-model="pageNo" @on-jump="jump" style="float:right;"></pager>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import Loader from "@/components/util/Loader";
import ItemStat from "@/old/api/item/itemstat";
import StatGrid from "@/old/game/StatGrid";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import pager from '@/components/util/Pager';
Vue.component("load-indicator", Loader);

export default {
    components: {
        GoBack,
        StatGrid,
        ItemIconTooltip,
        pager
    },
    data(){
        return{
            result:{
                titles:null,
            },
            loading:true,
            collectionLoading:true,
            noLink:false,
            searchName:null,
            collection:null,
            pageSize: 30,
            pageNo: 1,
        }
    },
    created(){
        this.fetchData();
    },
    watch:{
        searchName(val){
            console.log(val)
            if(val==""){
                this.fetchData();
            }
        }
    },
    methods:{
        jump(id){
            this.pageNo = id;
            this.fetchData();
        },
        fetchData(){
            if(GameInfo.titleCache[this.pageNo] && !this.searchName){
                this.result = GameInfo.titleCache[this.pageNo];
                this.collection = GameInfo.titleCache[1].reward;
                this.loading = false;
                this.collectionLoading = false;
            }else{
                this.loading = true;
                GameInfo.titleInfo(this.searchName,this.pageNo,this.$store.state.regionCode,
                    (res) => {
                        this.pageSize = Math.floor(res.count/10) + 1;
                        this.result = res;
                        this.loading = false;
                        this.collection = res.reward;
                        this.collectionLoading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                        this.collectionLoading = false;
                });
            }
        },
        statSet(state) {
            let ret = ItemStat.joinStatSet(state,"value");
            return ret;
        },
        titleStyle(textincolor,textoutcolor){
            //const title = this.loading.result!;
            const fg = `color: ${GameInfo.hexToColor(textincolor)}`;
            const bg = `text-shadow: 0px 0px 2px ${GameInfo.hexToColor(textoutcolor)}`;
            return `${fg};${bg};`;
            //return '';
        },
        searchData(){
            if(!this.searchName) return;
            this.loading = true;
            this.pageNo = 1;
            GameInfo.titleInfo(this.searchName,this.pageNo,this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                        this.pageSize = Math.floor(res.count/10) + 1;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
            });
        },
        resetfield(){
            this.searchName = '';
            this.pageNo = 1;
            this.pageSize = GameInfo.titleCache[1].count;
        }
    }
}
</script>
<style lang="less" scoped>
button{
    height:1.8em;
    margin-left:5px;
}
</style>