<template>
    <div>
        <h1 class="divided">誓约</h1>
        <go-back />
        <div v-if="loading">
            <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <h3>举办地点：{{result.hall.map}}</h3>
            <div>共有3个入场频道，分别是{{result.hall.channelName}}</div>
            <h3>开放时间：</h3>
            <div><span style="margin-right:1em;" v-for="(v,k) in result.hall.openTime" :key="k">{{v}}</span></div>

            <h3>共有{{setCount}}种套餐</h3>
            注：
            <p>自行点击物品 可查看戒指属性，buff属性等。</p>
            <p>离婚后，除了动作可拥有，其他物品均销毁或无效果</p>
            <table style="margin:0 auto;" border="1">
                <thead>
                    <th>套餐</th>
                    <th>可邀请人数</th>
                    <th>物品</th>
                    <th>称号</th>
                </thead>
                    <tr v-for="(v,k) in this.result.set" :key="k">
                        <td>{{k + 1}}</td>
                        <td>{{v.invite}}人</td>
                        <td>
                            <div class="Items">
                                <div class="item" v-for="(vv,kk) in v.info" :key="kk">
                                    <template v-if="vv.type!=3">
                                        <item-icon-tooltip
                                            :itemId="vv.id" 
                                            :itemStub="vv.item"
                                            :count="vv.count"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </template>
                                </div>
                            </div>
                        </td>
                        <td>
                            <div>
                                <div v-for="(vv,kk) in v.info" :key="kk">
                                    <template v-if="vv.type==3">
                                        <div :style="titleStyle(vv.title._TextInColor,vv.title._TextOutColor)">{{vv.title.name}}</div>
                                    </template>
                                </div>
                            </div>
                        </td>
                    </tr>
            </table>
            <h3>誓约商店</h3>
            <shop-browser :shopId="shopId"  :useClick="useClick" />
        </template>
    </div>
</template>
<script>
import GoBack from "@/components/util/GoBack.vue";
import GameInfo from '../api/GameInfo';
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
export default {
    components: {
        GoBack,
        ItemIconTooltip,
        ShopBrowser
    },
    data(){
        return{
            result:{
                hall:{
                    map:null
                },
                set:null,
            },
            loading:true,
            error:null,
            noLink:false,
            shopId:89081,
            useClick: true
        }
    },
    created(){
        this.fetchData()
    },
    computed:{
        setCount(){
            if(this.result.set){
                return this.result.set.length;
            }
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.promiseCache){
                this.result = GameInfo.promiseCache;
                this.loading = false;
            }else{
                GameInfo.PromiseInfo(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                });
            }
        },
        titleStyle(textincolor,textoutcolor){
            const fg = `color: ${GameInfo.hexToColor(textincolor)}`;
            const bg = `text-shadow: 0px 0px 2px ${GameInfo.hexToColor(textoutcolor)}`;
            const margin = 'margin-bottom:10px;'
            return `${fg};${bg};${margin};`;
        },
    }
}
</script>
<style lang="less" scoped>
.Items {
    display: flex;
    flex-direction: row;

    .item {
        flex: 0 0 auto;
    }
}

tr{
    height:7rem;
}
</style>