<template>
    <div>
        <go-back />
        <h1 class="divided">称号收藏册</h1>
        <div v-if="loading">
            <load-indicator loadText="正在加载称号"></load-indicator>
        </div>
        <template v-if="!loading">
            注：下列称号具体属性自行到称号大全搜索即可
            <h3 style="text-align:center;">收藏进度</h3>
            <table style="margin:0 auto;">
                <thead>
                    <th>阶段</th>
                    <th>收集数</th>
                    <th>奖励</th>
                </thead>
                <tr v-for="(v,k) in result.reward" :key="k">
                    <td>{{v.id}}</td>
                    <td>{{v.goal}}</td>
                    <td>
                        <span v-for="(vv,kk) in v.item" :key="kk">
                            <item-icon-tooltip
                                :itemId="vv.item.id" 
                                :itemStub="vv.item"
                                :count="vv.item.count"
                                :noLink="noLink">
                            </item-icon-tooltip>
                        </span>
                    </td>
                </tr>
            </table>
            
            <table border = 2>
                <thead>
                    <th>称号</th>
                    
                    <th>物品</th>
                    <th style="min-width:200px">获得条件</th>
                    <th>描述</th>
                </thead>
                    <tr v-for="(v,k) in result.collection" :key="k">
                        <td><span :style="titleStyle(v.color)">{{v.name}}</span></td>
                        
                        <td>
                            <item-icon-tooltip
                                :itemId="v.reward[0].item.id" 
                                :itemStub="v.reward[0].item"
                                :count="v.reward[0].item.count"
                                :noLink="noLink">
                            </item-icon-tooltip>
                            </td>
                        <td><ul v-for="(v1,k1) in v.titles" :key="k1" class="fa-ul">
                                <li><span class="fa-li"><i class="fa fa-check" style="color:#42d642;"></i></span>{{v1.name}}</li>
                            </ul>
                        </td>
                        <td>{{v.desc}}</td>
                    </tr>
            </table>
            <pager :pageSize="pageSize" v-model="pageNo" @on-jump="jump" style="float:right;"></pager>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import Loader from "@/components/util/Loader";
import ItemStat from "@/old/api/item/itemstat";
import StatGrid from "@/old/game/StatGrid";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import pager from '@/components/util/Pager';
Vue.component("load-indicator", Loader);

export default {
    components: {
        GoBack,
        StatGrid,
        ItemIconTooltip,
        pager
    },
    data(){
        return{
            result:{
                collection:null,
                reward:null
            },
            loading:true,
            noLink:false,
            pageSize: 30,
            pageNo: 1,
        }
    },
    created(){
        this.fetchData();
    },
    methods:{
        jump(id){
            this.pageNo = id;
            this.fetchData();
        },
        fetchData(){
            if(GameInfo.titleBookCache[this.pageNo]){
                this.result = GameInfo.titleBookCache[this.pageNo];
                this.loading = false;
            }else{
                this.loading = true;
                GameInfo.titleBook(this.$store.state.regionCode,this.pageNo,
                    (res) => {
                        this.pageSize = Math.floor(res.count/10) + 1;
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                });
            }
        },
        statSet(state) {
            let ret = ItemStat.joinStatSet(state,"value");
            return ret;
        },
        titleStyle(color){
            let textincolor;
            if(color == 3){
                textincolor = '0xFF99CC00';
            }else if(color == 4){
                textincolor = '0xFFFFCC00';
            }
            const fg = `color: ${GameInfo.hexToColor(textincolor)}`;
            const bg = `text-shadow: 0px 0px 2px`;
            return `${fg};${bg};`;
            //return '';
        }
    }
}
</script>