<template>
    <div>
        <h1 class="divided">交易税率</h1>
        <go-back />
        <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <table style="margin:auto;" border="2">
                <thead>
                    <th>税务类型</th>
                    <th>税率</th>
                    <th>固定征税额</th>
                    <th>输入值(金)</th>
                    <th>计算结果(金)</th>
                </thead>
                <tr v-for="(v,k) in result" :key="k" style="height:70px;">
                    <td>{{v._TaxSort | taxName}}</td>
                    <td>{{v._Rate | rate}}%</td>
                    <td>{{v._TaxAmount | gold}}</td>

                    <td v-if="v._Rate!=0 && v._TaxAmount ==0 "><input type="text" v-model="inputNum[k]" class="inputcal"/></td>
                    <td v-else>-</td>

                    <td v-if="v._Rate!=0 && v._TaxAmount ==0 " style="font-weight:bold;">{{ taxCount(inputNum[k],v._Rate) }}</td>
                    <td v-else-if="v._Rate==0 && v._TaxAmount !=0 " style="font-weight:bold;">{{v._TaxAmount | gold}}</td>
                    <td v-else>0</td>
                </tr>
            </table>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import Loader from "@/components/util/Loader";
Vue.component("load-indicator", Loader);
export default {
    components: {
        GoBack,
    },
    created(){
        this.fetchData();
    },
    data(){
        return{
            result:null,
            loading:true,
            inputNum: [],
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.taxCache){
                this.result = GameInfo.taxCache;
                this.loading = false;
            }else{
            GameInfo.Tax(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        taxCount(input,rate){
            let inputs = input ? input: 0
            return Math.round(inputs * rate);
        }
    },
    filters:{
        taxName(num){
            switch (num) {
                case '1':
                    return '个人交易金币'
                case '2':
                    return '交易所登记金币'
                case '3':
                    return '交易所结算金币'
                case '4':
                    return '发邮件'
                case '5':
                    return '邮件金币'
                case '7':
                    return '交易所登记(尊享券)'
                case '8':
                    return '交易所结算龙币'
                case '9':
                    return '仓转手续费'
                default:
                    return '其它'
            }
        }
    },
}
</script>
<style lang="less" scoped>
    .inputcal{
        text-align: center; font-size:1.1rem;
    }
</style>