<template>
    <div>
        <h1 class="divided">分裂迷宫</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div>
                <h3>迷宫入场</h3>
                <table class="labryinth-table">
                    <thead>
                        <th>层数</th>
                        <th>需要门票</th>
                    </thead>
                        <tr v-for="(v,k) in result.enter" :key="k" style="height:70px;">
                            <td>{{v._ExDifficultyTypeMin | floorName}} ~ {{v._ExDifficultyTypeMax | floorName}}</td>
                            <td><div v-if="v.item && Object.keys(v.item).length">
                                    <item-icon-tooltip
                                            :itemId="v.item.id" 
                                            :itemStub="v.item"
                                            :count="v.count"
                                            :noLink="noLink">
                                    </item-icon-tooltip>
                                </div>
                                <div v-else>无</div>
                            </td>
                        </tr>
                </table>
            </div>    

            <div>
                <h3>每周结算</h3>
                    <table border="1" class="labryinth-table">
                        <thead>
                            <th>层数</th>
                            <th>奖励</th>
                        </thead>
                        <tr v-for="(v,k) in result.weekly" :key="k">
                            <td style="width:140px;">{{v.floor}}</td>
                            <td>
                                <div class="Title">
                                    <div v-for="(vv,kk) in v.info" :key="kk" class="titleitem">
                                        <item-icon-tooltip
                                                :itemId="vv.item.id" 
                                                :itemStub="vv.item"
                                                :count="vv.count"
                                                :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                        </tr>
                </table>
            </div>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip
    },
    data(){
        return{
            result:{
                weekly:null,
                enter:null
            },
            noLink:false,
            loading:true,
        }
    },
    filters:{
        floorName(val){
            let floor = parseInt(val);
            switch (floor) {
                case 0:
                    return '简单'
                case 1:
                    return '普通'
                case 2:
                    return '困难'
                case 3:
                    return '大师'
                case 4:
                    return '深渊'
                default:
                    return floor-4 + `层`;
            }
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.labyrinthCache){
                this.result = GameInfo.labyrinthCache;
                this.loading = false;
            }else{
            GameInfo.Labyrinth(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
</style>