    titleCache:[],
    titleInfo(name,page,region, okcb, errcb){
        region = ensureRegion(region);
        ApiHttpClient.get(`title.php?lan=${region}&page=${page}`,
        {
            params:{
                name:name? name:undefined
            }
        }).then(
        (res) => {
            if(!name){
                this.titleCache[page] = res.data;
            }
            okcb(res.data);
        }, 
        errcb);
    },
