<template>
    <div>
        <h1 class="divided">天梯</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <template v-if="result.basic.season!=0">
            <h3>当前赛季：第{{result.basic.season}}季  </h3>
            <h3>时间：{{result.basic.start}} ~ {{result.basic.end}} </h3>

            <h4>下一赛季：第{{result.basic.nextSeason}}季  </h4>
            <h4>时间：{{result.basic.nextStart}} ~ {{result.basic.nextEnd}} </h4>
            
            <div class="section">
                <div class="title">开放时间</div>
                <table border="1">
                    <thead>
                        <th>每周</th>
                        <th>开放时间</th>
                    </thead>
                        <tr v-for="(v,k) in result.schedule" :key="k">
                            <td>{{v._Weekday}}</td>
                            <td>{{v.label}}</td>
                        </tr>
                </table>
            </div>
            <div class="section">
                <div class="title">赛季信息</div>
                <table border="1" class="laddertable">
                    <thead>
                        <th style="width:180px;">天梯等级</th>
                        <th>排位</th>
                        <th>奖励</th>
                        <th>惩罚机制</th>
                    </thead>
                        <tr v-for="(v,k) in result.rank" :key="k">
                            <td>
                                <div style="display:flex; align-items:center;">
                                <div class="item-icon">
                                    <span class="item" :style="`background:url(${iconurl}) -${getIcon(v.rankId).x}px -${getIcon(v.rankId).y}px`"></span>
                                </div>
                                <span style="margin-left:1rem;">{{v.name}}</span>
                                </div>
                            </td>

                            <td>{{v.label}}</td>
                            <td><div class="Title">
                                    <div class="titleitem" v-for="(vv,kk) in v.reward" :key="kk">
                                        <item-icon-tooltip
                                            :itemId="vv.item.id" 
                                            :itemStub="vv.item"
                                            :count="vv.count"
                                            :noLink="noLink">
                                        </item-icon-tooltip>
                                    </div>
                                </div>
                            </td>
                            <td v-if="v.PenaltyDay == 0">无</td>
                            <td v-else>连续{{v.PenaltyDay}}天后不打，则每天扣{{v.PenaltyValue}}分</td>
                        </tr>
                </table>
            </div>
            <div class="section">
                <div class="title">每周奖励</div>
                <table border="1">
                    <thead>
                        <th>天梯积分区间</th>
                        <th>天梯点数</th>
                    </thead>
                        <tr v-for="(v,k) in result.reward" :key="k">
                            <td>{{v._LadderscoreMin}} ~ {{v._LadderscoreMax}} 分</td>
                            <td>{{v._LadderPoint}}</td>
                        </tr>
                </table>
            </div>
            <div class="section">
                <div class="title">天梯商店</div>
                <div align="center" style="margin-bottom:30px;">
                <button v-for="(item, index) in ['战士', '弓箭', '法师', '牧师', '学者', '舞娘', '刺客', '萌骑士', '兽娘', '浪客']" :key="index" 
                @click="goShop(item)"
                 >{{item}}</button>
                </div>

                <div v-if="shopId!=0">
                    <shop-browser :shopId="shopId"  :useClick="useClick" />
                </div>

            </div>
            
            </template>
            <template v-else>
                <h3>当前赛季为免费季  </h3>

                <h4>下一赛季：第{{result.basic.nextSeason}}季  </h4>
                <h4>时间：{{result.basic.nextStart}} ~ {{result.basic.nextEnd}} </h4>
            </template>
        </template>
    </div>
</template>

<script>
import Vue from 'vue';
import GoBack from "@/components/util/GoBack.vue";
import GameInfo from '../api/GameInfo';
import Loader from "@/components/util/Loader";
import { ApiHttpClient } from "@/globals";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
Vue.component("load-indicator", Loader);
export default {
    components: {
        GoBack,
        ItemIconTooltip,
        ShopBrowser
    },
    data(){
        return{
            result:{
                basic:{
                    season:null,
                    start:null,
                    end:null,
                    nextSeason:null,
                    nextStart:null,
                    nextEnd:null,
                },
                schedule:null,
                reward:null,
                rank:null,
            },
            loading:true,
            iconurl:`${ApiHttpClient.defaults.resourceURL}/dds/tier.png`,
            noLink:false,
            shopId: 0,
            useClick: true
        }
    },
    created(){
        this.fetchData();
    },
    computed:{
        iconLocate(){
            return function(pageIdx){
                let row = Math.floor(pageIdx / 4);
                let column = pageIdx % 4;
                const UNIT_SIZE = 64;

                let ret = {
                    x: Math.max(UNIT_SIZE * column, 0),
                    y: UNIT_SIZE * row,
                };
                return ret;
            }
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.ladderCache){
                this.result = GameInfo.ladderCache;
                this.loading = false;
            }else{
                GameInfo.ladderInfo(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        getIcon(pageIdx){
            let row = Math.floor(pageIdx / 4);
            let column = pageIdx % 4;
            const UNIT_SIZE = 64;

            let ret = {
                x: Math.max(UNIT_SIZE * column, 0),
                y: UNIT_SIZE * row,
            };
            return ret;
        },
        goShop(val){
            switch (val) {
                case '战士':
                    this.shopId = 5101;
                    break;
                case '弓箭':
                    this.shopId = 5102;
                    break;
                case '法师':
                    this.shopId = 5103;
                    break;
                case '牧师':
                    this.shopId = 5104;
                    break;
                case '学者':
                    this.shopId = 5105;
                    break;
                case '舞娘':
                    this.shopId = 5106;
                    break;
                case '刺客':
                    this.shopId = 5107;
                    break;
                case '萌骑士':
                    this.shopId = 5108;
                    break;
                case '兽娘':
                    this.shopId = 5109;
                    break;
                case '浪客':
                    this.shopId = 5110;
                    break;
                default:
                    break;
            }
        }
        
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.section {
        position: relative;
        margin-top: 35px;
        border-top: 1px solid @dv-c-accent-1;
        padding-top: 4px;
        .title {
            font-size: 12px;
            color: @dv-c-accent-1;
            text-transform: uppercase;
            letter-spacing: 0.2em;
            position: absolute;
            top: -1.3em;
            .left(0);
            user-select: none;
        }
        button{
            margin-left:0.6em;
            margin-top:0.6em;
        }
}

.item-icon {
    width: 64px;
    height: 64px;
    position: relative;
    display: inline-block;

    .item {
        position: absolute;
        top: 1px;
        .left(1px);
        .right(0);
        bottom: 0;
    }
}

.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}
tr{
    height:2em;
}

.laddertable{
    margin: 0 auto;
}
</style>